
a {
  text-decoration: none;
    cursor: pointer;
    color: #08c;
}
ul {
  display: table;
  padding: 0 0 1em 1.5em;
  font-family: monospace;
}
li {
  list-style: none;
  float: left;
  width: 330px;
  margin-bottom: .5em;
  cursor: pointer;
}
.continer {
  width: 100%;
}
.header {
  overflow: hidden;
  position: relative;
  height: 70px;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding: 10px 0 7px 0;
}
.header>.title {
  padding-top: 10px;
}
.header>.searchIpt {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 20px;
  border: 1px solid rgba(0, 0, 0, .2);
  height: 30px;
  width: 500px;
  font: inherit;
  padding: 5px 15px;
}
.header>.searchIpt:focus {
  outline: none;
  border: 1px solid #3b99fc;
  -webkit-box-shadow: 0px 0px 10px 0px #3b99fc;
          box-shadow: 0px 0px 10px 0px #3b99fc;
}
.info {
  margin-top: 8px;
  height: 80px;
  line-height: 80px;
  padding: 0 1em;
    color: #6a737d;
    border-left: 0.25em solid #6a737d;
    background-color: #ccc;
}
.emoji{
  width: 22px !important;
  height: 22px !important;
  position: relative;
  top: 9px;
}
.emoji-category {
  margin: 20px 0;
}
.emoji-item:hover > div {
  margin-left: -5px;
}
.emoji-code {
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: transparent;
  cursor: pointer;
  padding: 10px 10px 10px 0;
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
  -webkit-rtl-ordering: logical;
}
div.copied::after {
  content: 'Copied';
  color: #63bf8a;
  overflow: hidden;
  -webkit-animation-duration: 1.8s;
          animation-duration: 1.8s;
  -webkit-animation-name: fadeOut;
          animation-name: fadeOut;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}
.footer {
  border-top: 1px solid rgba(0, 0, 0, .2);
  height: 40px;
  text-align: center;
  padding-top: 10px;
  color: #ccc;
  font-size: 12px;
}
@keyframes fadeOut {
0% {opacity: 1;
}
70% {opacity: .7;
}
100% {opacity: 0;
}
}
@-webkit-keyframes fadeOut {
0% {opacity: 1;
}
70% {opacity: .7;
}
100% {opacity: 0;
}
}




